import axios from '@axios'

export default {
  namespaced: true,
  state: {
    actividad_id: 0,
    curso_taller_id: '',
    tallerGlobal_id: '',
    estudiantes: [], // participante_id, DNI, PARTICIPANTE, EMAIL, PUEBLO_INDIGENA
    evidencias: [], // id, nombre, archivo
    actividadesTallerDeportivo: [], // id,  nombre, fecha, descripcion
  },
  getters: {},
  mutations: {
    listarEstudiantes(state, data) {
      state.estudiantes = data
      console.log('_______________-listar estudiantes: ', state.estudiantes)
    },
    listarEvidencias(state, data) {
      state.evidencias = data
      console.log('_______________-listar evidencias: ', state.evidencias)
    },
    listarActividades(state, data) {
      state.actividadesTallerDeportivo = data
      console.log('_______________-listar atividades: ', state.actividadesTallerDeportivo)
    },
  },
  actions: {
    fetchActividades(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/actividad`)
          .then(response => { ctx.commit('listarActividades', response.data.data); console.log('actividades GET: ', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    getActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/actividad/${id}`)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/actividad`, data)
          .then(response => { console.log('ACTIVIDAD CREADA:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateActividad(ctx, data) {
      console.log('CTX UPDATE ACTIVIDAD: ', ctx)
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/actividad/${ctx.state.actividad_id}`, data)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/actividad/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    searchParticipante(ctx, dni) {
      console.log('DNI :', dni)
      return new Promise((resolve, reject) => {
        axios
          .get(`/buscar-persona/${dni}`, { withCredentials: false })
          .then(response => { console.log('responseSearchParticipante: ', response); resolve(response) })
          .catch(error => reject(error))
      })
    },

    /* estudiantes */
    // eslint-disable-next-line no-unused-vars
    fetchEstudiantes(ctx, id) {
      console.log('idque llega a fetchEstudiantes: ', ctx.state.curso_taller_id)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/matricula`)
          .then(response => { ctx.commit('listarEstudiantes', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    fetchAsistenciaEstudiantes(ctx) {
    //  console.log('idque llega a fetchEstudiantes: ', ctx.state.tallerGlobal_id)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/actividad/${ctx.state.actividad_id}/participante`)
          .then(response => { console.log('estudiantesAsistencia: ', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateAsistenciaEstudiantes(ctx, data) {
      //  console.log('idque llega a fetchEstudiantes: ', ctx.state.tallerGlobal_id)
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/taller/actividad/${ctx.state.actividad_id}/participante`, data)
          .then(response => { console.log('estudiantesAsistenciaActualizado: ', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    /*  addParticipante(ctx, data) {
      console.log('valores data= ', data)
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/actividad/${data.actividad_id}/participante`, data)
          .then(response => { console.log('exitoso:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteParticipante(ctx, id) {
      console.log('registro a eliminar:', id)
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/actividad/participante/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    }, */
    /* evidencias */
    fetchEvidencias(ctx, id) {
      // console.log('idque llega a fetchEstudiantes: ', id)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/actividad/${id}/evidencia`)
          .then(response => { console.log('respuestaArchivos: ', response); ctx.commit('listarEvidencias', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addEvidencia(ctx, data) {
      console.log('ctx :', ctx)
      console.log('valores evidencia= ', data)
      const config = { headers: { 'Content-Type': 'multipart/formdata' } }
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/taller/actividad/${data.get('id')}/evidencia`, data, config)
          .then(response => { console.log('exitoso evidencia:', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteEvidencia(ctx, id) {
      console.log('registro a eliminar:', id)
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/taller/actividad/evidencia/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchCursoTaller(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`servicio-cultural/taller-global/${ctx.state.tallerGlobal_id}/taller/${ctx.state.curso_taller_id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
